
.abn-header{
    width: 170px;
    margin: 0 auto;
    height: 85px;
}
.abn-body{
    background-color: #f9f9f9;
    padding: 50px;
}

.main-body-abn{
    background: #f5f5f5;
    border:1px solid #ddd;
    padding: 0px;

}
#header{
    padding: 10px;
    margin: 0px;
    border-bottom:1px solid #ddd;
    color: #005E5D;

}

.abn-body-container{

    padding: 20px;
    /*border: 1px solid #ddd;*/
}

.abn-body-container .form-container{
    padding: 20px;
    background-color: #fff;
}

.how-to-login span{
    background-color: #f5f5f5;
    padding: 10px;
    display: inline-block;
    width: 80px;
    height: 80px;
    border: 1px solid #ddd;
    cursor: pointer;
    transition: 300ms;
}
.how-to-login span.active{
    background: white;
    border: 1px solid #969696;

}
.inputs-container span{
    display: inline-block;
    font-size: 16px;
}
.inputs-container input{
    border: 1px solid black;
    padding: 6px 15px;
    font-size: 16px;

}

.abn-body-container ol{
    padding-left: 15px;
}

.abn-body-container .row{
    margin-bottom: 15px;
}
.abn-body-container input[type="checkbox"]{
    display: none;
}
.account-number-static{
    padding: 6px 15px;
    border:1px solid black;
    border-right-color: transparent;
    background: #f5f5f5;
}

.account-input input{
    border-left-color: transparent;

}
.label-container{
    padding-top: 6px;
}

.handle-checkbox{
    display: inline-block;
    width: 17px;
    height: 17px;
    border: 1px solid #888;
    border-radius: 3px;
    background-color: #fff;
    margin-top: 6px;
    position: relative;
    top: 3px;
    margin-right: 6px;
}

.abn-body-container input[type="checkbox"]:checked + label .handle-checkbox:after{
    content: "";
    display: inline-block;
    position: absolute;
    top: -1px;
    left: -1px;
    width: 17px;
    height: 17px;
    background: url(../../static/media/abn-amro-checked.e9a2b8f4.svg) no-repeat center;
    background-size: 12px 12px;
}

.newcode{
    margin-top: 15px;
    color: #009286;
    text-decoration: underline;
    display: inline-block;

}

.newcode:hover{
    color: #f60;
    text-decoration: underline;
}

.getfeed-btn{
    padding-top: 20px;
}
.react-code-input-container{
    border:1px solid black;
}

.react-code-input{
    padding: 0px 30px;
}

.react-code-input-container input{
    border: none!important;
    width: 20px!important;
    height: 27px!important;
    padding: 1px;
    padding-left: 5px!important;
}

.inputs-container input:focus ,.inputs-container input:active{
    background-color: #ffc;
}

.input-errors-anb{
    color: #f60;
    margin-top: 5px;
}

.code-input input{
    letter-spacing:12px;
}


.code-input input::-webkit-input-placeholder { /* Edge */
    color: #707071;
    font-size:12px;
}

.code-input input::-ms-input-placeholder {
    color: #707071;
    font-size:10px;
}

.code-input input::placeholder {
    color: #707071;
    font-size:10px;
}

.terms-condition{
    margin-bottom: 0px;
}


.bank-submit{
    margin-top: 20px;
    width: 226px;
    font-size: 14px!important;
}

.server-error{
    color:red;
    font-size: 13px;
}



.spinner-container{
    position: absolute;
    width: 100%;
    height: 100%;
    background: #ffffffad;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    z-index: 1;
    top: 0px;
    left: 0px;
}

.loading-spinner{
    background: white;
    top: 50%;
    left: 50%;
}



/*Dialog*/
.dialog-container-text{
    font-size: 17px;
}

.title-dialog i{
    font-size: 35px;
    color:green;
}
.submit-btn-dialog{
    -webkit-justify-content: center;
            justify-content: center;
}

.submit-btn-dialog button{
    font-size: 12px;
}
/*Dialog*/

.account-access input{
    width: 100%;
    border: none;
    outline: none;
    box-shadow: none;
    padding: 0px 5px;
}

.border-input-rabo{
    border-right: 1px solid transparent;
    border-bottom: 1px solid #AAA;
    border-left: 1px solid transparent;
    border-bottom-right-radius: 16px;
    border-bottom-left-radius: 16px;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    /*padding: 12px 102px 4px 14px;*/
}
.account-access label{
    text-align: left;
    color: #999;

}

.border-input-rabo{
    color: #333;
    padding-top:13px;
    font-size: 15px;
    padding-bottom: 3px;

}
.border-input-rabo.has-focus{
    border-bottom: 2px solid #009;
}
.border-input-rabo.has-error{
    border-bottom: 2px solid red;
}
.more-info-rabo{
    padding-top:13px;


}
.more-info-rabo i{
    font-size: 16px;
}

.card-account-constant-height{
    height: 103px;
}

.input-errors-rabo{
    text-align: center;
    color: red;
    display: inline-block;

}

.abn-body-container input[type="checkbox"]:checked + label .handle-checkbox:after{
    content: "";
    display: inline-block;
    position: absolute;
    top: -1px;
    left: -1px;
    width: 17px;
    height: 17px;
    background: url(../../static/media/rabo-checked.4e49be63.svg) no-repeat center;
    /*background-size: 12px 12px;*/
}
.how-to-login{
    margin-top: 21px;
    line-height: 23px;
    font-size: 15px;
    padding-left: 15px;
}
.how-to-login.deactive{
    color: #DDD;
}

.border-input-rabo input[disabled]{
    background: white;
}


.bank-submit-account-info{
    width: 130px;
}


.rabo-bank-account-update .border-input-rabo input[disabled]{
    color: #c3c3c3!important;
}


